<template>
  <v-app>
    <v-navigation-drawer
      v-if="$session.user != null"
      :permanent="setDrawerDesign(false)"
      :app="setDrawerDesign(false)"
      :clipped="setDrawerDesign(false)"
      :temporary="setDrawerDesign(true)"
      :absolute="setDrawerDesign(true)"
      class="secondary"
      :width="setDrawerSize()"
      v-model="showDrawer"
      height="100vh"
    >
      <template v-slot:prepend>
        <v-list-item link to="/">
          <v-list-item-content>
            <v-list-item-title
              class="display-1 text-center white--text font-weight-bold"
              >항만 안전</v-list-item-title
            >
          </v-list-item-content>
        </v-list-item>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in menu_list"
          router-link
          :to="item.to"
          :key="index"
          :class="
            $route.fullPath === item.to
              ? 'point2'
              : item.to !== '/' && $route.fullPath.indexOf(item.to) !== -1
              ? 'point2'
              : 'secondary'
          "
        >
          <v-list-item-content>
            <v-list-item-title class="white--text">{{
              item.name
            }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <template v-slot:append>
        <div class="pa-2">
          <v-btn depressed block class="point2 white--text" @click="logout()">
            Logout
          </v-btn>
        </div>
      </template>
    </v-navigation-drawer>

    <v-main class="primary">
      <Alert
        :dialogCheck="alertView"
        @dialog-event="dialogEvent"
        :value="value"
      />
      <div v-if="$session.user != null">
        <v-app-bar v-if="isAppBarIcon()" flat class="primary">
          <v-app-bar-nav-icon
            @click.stop="showDrawer = !showDrawer"
          ></v-app-bar-nav-icon
        ></v-app-bar>
        <v-progress-circular
          class="load"
          v-if="$session.load"
          indeterminate
          color="green"
        ></v-progress-circular>
        <router-view
          :band_data="band_data"
          :gateway_data="gateway_data"
          class="router-view"
        />
      </div>

      <login v-else />
    </v-main>
  </v-app>
</template>

<script>
import login from "./components/Login.vue";
import Alert from "./components/Alert.vue";
export default {
  components: { login, Alert },
  async mounted() {
    setTimeout(async () => {
      if (this.$session.user != null) await this.getGatewayState();
    }, 1000);
    this.$session.$on("data", async (data) => {
      if (this.$session.user != null) {
        var bands = await this.$http.get(
          "users/bandinfo/" + this.$session.user.id
        );
        bands = bands.data.data;
        for (var i = 0; i < bands.length; i++) {
          if (bands[i].bid == data.extAddress.high) {
            if (data.active === "true") {
              if (
                this.bandsclose[bands[i].bid] != null &&
                this.bandsclose[bands[i].bid] != undefined
              ) {
                clearTimeout(this.bandsclose[data.bid]);
              }
              this.bandsclose[data.bid] = null;
            }
            if (data.bandData.scdState != 0 && data.bandData.scdState != 1) {
              if (
                this.bandsscd[data.bid] != undefined &&
                this.bandsscd[data.bid] != null
              ) {
                clearTimeout(this.bandsscd[data.bid]);
              }
              this.bandsscd[data.bid] = null;
            } else if (
              data.bandData.scdState === 0 ||
              data.bandData.scdState === 1
            ) {
              if (
                this.bandsscd[data.bid] == undefined ||
                this.bandsscd[data.bid] == null
              ) {
                this.value.subtitle = bands[i].bid;
                this.bandsscd[data.bid] = setTimeout(() => {
                  this.value.title = "미착용 감지";
                  this.value.content = 5000;
                  this.$toast.warning(
                    this.value.subtitle + " " + this.value.title,
                    {
                      position: "top-right",
                      timeout: this.value.content,
                      closeOnClick: true,
                      pauseOnFocusLoss: true,
                      pauseOnHover: true,
                      draggable: true,
                      draggablePercent: 0.6,
                      showCloseButtonOnHover: false,
                      hideProgressBar: true,
                      closeButton: "button",
                      icon: true,
                      rtl: false,
                    }
                  );
                  clearTimeout(this.bandsscd[data.bid]);
                  this.bandsscd[data.bid] = null;
                }, 30000);
              }
            }
          }
        }
        this.band_data = data;
      }
    });
    this.$session.$on("gateway_connect", async (data) => {
      if (this.$session.user != null) {
        var res = await this.checkUsersGateways(data);
        if (res === true) {
          if (data.connectstate) {
            if (data.state === 0) {
              this.gatewayAlert(data);
            } else {
              if (this.gwstate != true) {
                this.gatewayAlert(data);
              }
            }
            this.gwstate = true;
          }

          var index = this.gateway_data
            .map(function(g) {
              return g["panid"];
            })
            .indexOf(data.panid);
          if (index === -1) {
            this.gateway_data.push(data);
          } else {
            this.gateway_data[index] = data;
          }
        }
      }
    });
    //  this.$session.$on("gateway_disconnect", (data) => {
    //   if (this.$session.user != null) {
    //     this.gateway_data = data;
    //   }
    // });
    this.$session.$on("efwbasync", this.onAlertCheck.bind(this));
  },
  methods: {
    async getGatewayState() {
      try {
        await this.$http.get("gateway/bandnum");
      } catch (ex) {
        console.log(ex);
      }
    },
    async onAlertCheck(data) {
      var pass = false;
      if (this.$session.user != null) {
        var bands = await this.$http.get(
          "users/bandinfo/" + this.$session.user.id
        );
        bands = bands.data.data;
        for (var i = 0; i < bands.length; i++) {
          if (bands[i].bid == data.bid) pass = true;
        }
        if (pass) this.makeMessage(data);
      }
    },
    async checkUsersGateways(data) {
      try {
        var res = await this.$http.post("usersgateways/check", {
          uid: this.$session.user.id,
          pid: data.panid,
        });
        return res.data.data;
      } catch {
        return false;
      }
    },
    gatewayAlert(data) {
      this.$toast.success(
        "[GW] " + data.panid + "가 " + data.ip + "로 연결되었습니다.",
        {
          position: "top-right",
          timeout: 10000,
          closeOnClick: true,
          pauseOnFocusLoss: true,
          pauseOnHover: true,
          draggable: true,
          draggablePercent: 0.6,
          showCloseButtonOnHover: false,
          hideProgressBar: true,
          closeButton: "button",
          icon: true,
          rtl: false,
        }
      );
    },
    makeMessage(data) {
      this.value.subtitle = data.bid;
      switch (data.type) {
        case 0: {
          this.value.title = "낙상 감지";
          this.value.content = 0;
          this.$toast.error(this.value.subtitle + " " + this.value.title, {
            position: "top-right",
            timeout: this.value.content,
            closeOnClick: true,
            pauseOnFocusLoss: true,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: true,
            closeButton: "button",
            icon: true,
            rtl: false,
          });
          break;
        }
        case 1: {
          this.value.title = "수신 감도";
          this.value.content = 5000;
          this.$toast.warning(this.value.subtitle + " " + this.value.title, {
            position: "top-right",
            timeout: this.value.content,
            closeOnClick: true,
            pauseOnFocusLoss: true,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: true,
            closeButton: "button",
            icon: true,
            rtl: false,
          });
          break;
        }
        case 2: {
          if (this.bandsbattery[data.bid] != undefined) {
            if (this.bandsbattery[data.bid] >= 2) {
              this.value.title = " 배터리 용량 부족";
              this.value.content = 5000;
              this.$toast.warning(
                this.value.subtitle + " " + this.value.title,
                {
                  position: "top-right",
                  timeout: this.value.content,
                  closeOnClick: true,
                  pauseOnFocusLoss: true,
                  pauseOnHover: true,
                  draggable: true,
                  draggablePercent: 0.6,
                  showCloseButtonOnHover: false,
                  hideProgressBar: true,
                  closeButton: "button",
                  icon: true,
                  rtl: false,
                }
              );
            } else {
              this.bandsbattery[data.bid]++;
            }
          } else {
            this.bandsbattery[data.bid] = 0;
          }
          break;
        }
        case 3: {
          break;
        }
        case 4: {
          this.bandsbattery[data.bid] = 0;
          if (
            this.bandsclose[data.bid] == null ||
            this.bandsclose[data.bid] == undefined
          ) {
            this.bandsclose[data.bid] = setTimeout(() => {
              this.value.title = "연결 끊김";
              this.value.content = 0;
              this.$toast.error(this.value.subtitle + " " + this.value.title, {
                position: "top-right",
                timeout: this.value.content,
                closeOnClick: true,
                pauseOnFocusLoss: true,
                pauseOnHover: true,
                draggable: true,
                draggablePercent: 0.6,
                showCloseButtonOnHover: false,
                hideProgressBar: true,
                closeButton: "button",
                icon: true,
                rtl: false,
              });
              clearTimeout(this.bandsclose[data.bid]);
              this.bandsclose[data.bid] = null;
            }, 5000);
          }
          break;
        }
      }
    },
    // onAlertCheck(data) {
    //   this.bands[data.shortAddress - 1].bandvalue = data;
    //   if (this.$router.history.current.name != "Login") {
    //     var flag = false;
    //     for (var i = 0; i < this.bands.length; i++) {
    //       if (this.bands[i].bandvalue.bandData.fall_detect === 1) {
    //         flag = true;
    //         this.value.subtitle =
    //           "0x0" +
    //           this.bands[i].bandinfo.bid +
    //           " " +
    //           this.bands[i].bandinfo.name;
    //         this.alertView = true;
    //       }
    //     }
    //     if (!flag) {
    //       this.alertView = false;
    //     }
    //   }
    // },
    dialogEvent(check) {
      this.alertView = check;
    },
    isAppBarIcon() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs" || "sm":
          return true;
        default:
          return false;
      }
    },
    isDrawer() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs" || "sm":
          break;
        default:
          this.showDrawer = true;
          break;
      }
      return this.showDrawer;
    },
    logout() {
      this.$session.releaseSession();
    },
    setDrawerSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "180";
        case "sm":
          return "180";
        case "md":
          return "180";
      }
    },
    setDrawerDesign(type) {
      switch (this.$vuetify.breakpoint.name) {
        case "xs" || "sm":
          return type;
        default:
          return !type;
      }
    },
  },
  data: () => ({
    showDrawer: true,
    appBarIcon: false,
    startTime: null,
    bandsbattery: {},
    bandsclose: {},
    bandsscd: {},
    viewData: {
      value: true,
    },
    alertView: false,
    value: {
      title: "",
      subtitle: "",
      content: "",
      state: 0,
    },
    windowHeight: null,
    band_data: null,
    gateway_data: [],
    bandlist: [],
    gwstate: false,
    disconnect: 0,
    menu_list: [
      { name: "대시보드", to: "/" },
      { name: "밴드", to: "/band" },
      { name: "게이트웨이", to: "/gateway" },
      { name: "사용자", to: "/user" },
      { name: "로그", to: "/log" },
    ],
  }),
};
</script>
<style lang="scss">
.router-view {
  margin: 30px;
}
.switch_mode_image {
  float: right;
}
.load {
  top: 50%;
  left: 50%;
  position: absolute;
  display: block;
  z-index: 99;
  text-align: center;
  transform: translate(-50%, -50%);
}

// #nav {
//   padding: 30px;

//   a {
//     font-weight: bold;
//     color: #2c3e50;
//   }
// }

/* for MS계열 브라우저 */
@keyframes blink {
  0% {
    color: white;
  }

  50% {
    color: #ff5252;
  }
}

/* for Chrome, Safari */
@-webkit-keyframes blink {
  0% {
    color: white;
  }

  50% {
    color: #ff5252;
  }
}
/* blink CSS 브라우저 별로 각각 애니메이션을 지정해 주어야 동작한다. */
.Vue-Toastification__toast--error > svg {
  animation: blink 1s step-end infinite;
  -webkit-animation: blink 1s step-end infinite;
}
</style>
